import firebaseConfig from '../firebaseConfig.js';

const app = firebase.initializeApp(firebaseConfig);
const auth = firebase.getAuth(app);
const db = firebase.getFirestore(app);


// Luo kirjautumiskohdat
function createAuthUI() {
    const authContainer = document.createElement('div');
    authContainer.id = 'auth-container';
    
    const heading = document.createElement('h2');
    heading.textContent = 'Kirjaudu sisään';
    
    const emailInput = document.createElement('input');
    emailInput.type = 'email';
    emailInput.id = 'email';
    emailInput.placeholder = 'Sähköposti';

    const passwordInput = document.createElement('input');
    passwordInput.type = 'password';
    passwordInput.id = 'password';
    passwordInput.placeholder = 'Salasana';

    const loginButton = document.createElement('button');
    loginButton.id = 'login-button';
    loginButton.textContent = 'Kirjaudu sisään';

   

    authContainer.appendChild(heading);
    authContainer.appendChild(emailInput);
    authContainer.appendChild(passwordInput);
    authContainer.appendChild(loginButton);
    

    document.body.prepend(authContainer); // Lisää kirjautumiskohdat sivun alkuun
}

// Kirjautuminen
document.addEventListener('DOMContentLoaded', function() {
    createAuthUI(); // Luo kirjautumiskohdat

    document.getElementById('login-button').onclick = function() {
        const email = document.getElementById('email').value;
        const password = document.getElementById('password').value;

        signInWithEmailAndPassword(auth, email, password)
            .then((userCredential) => {
                // Käyttäjä on kirjautunut sisään
                document.getElementById('auth-container').style.display = 'none';
                document.getElementById('account-container').style.display = 'block';
                loadTransactions();  // Lataa aiemmat transaktiot
            })
            .catch((error) => {
                console.error("Virhe kirjautumisessa: ", error.message);
                alert("Kirjautumisvirhe: " + error.message);  // Anna käyttäjälle viesti
            });
    }

// Tulojen ja menojen lisääminen
document.getElementById('add-button').onclick = async function() {
    const stuff = document.getElementById('stuff').value; 

    if (stuff) {
        const apiKey = '8dda2a79a0ad82930338ad7693a4d551'; // Käytä omaa API-avaintasi
        const city = 'Helsinki';
        const url = `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${apiKey}`;

        try {
            const response = await fetch(url);
            if (!response.ok) {
                throw new Error('Virhe: ' + response.status);
            }
            const data = await response.json(); // Hanki JSON-vastaus

            const temp = data.main.temp; // Hanki lämpötila Kelvininä
            const weatherDescription = data.weather[0].description; // Hanki sääkuvaus

            // Näytä säätiedot käyttäjälle
            const weatherInfoDiv = document.getElementById('weather-info');
            weatherInfoDiv.innerHTML = `
                <h3>Säätiedot</h3>
                <p>Helsinki<p>
                <p>Lämpötila: ${(temp - 273.15).toFixed(2)} °C</p>
                <p>Kuvaus: ${weatherDescription}</p>
            `;
          
            // Tallenna tietokantaan
            await addDoc(collection(db, 'transactions'), {

                stuff: stuff, 
                timestamp: serverTimestamp()
            })

            document.getElementById('stuff').value = '';               
            alert("matkatavara on lisätty!");
            loadTransactions(); // Lataa aiemmat tiedot
        } catch (error) {
            console.error("Virhe API-kutsussa tai tietojen tallentamisessa: ", error.message);
            alert("Virhe: " + error.message);
        }
    } else {
        alert("Täytä kenttä: matkatavarat!");
    }
};


    // Lataa aiemmat transaktiot
    function loadTransactions() {
        const transactionsRef = collection(db, 'transactions');
        onSnapshot(transactionsRef, (querySnapshot) => {
            const transactionsList = document.getElementById('transactions-list');
            transactionsList.innerHTML = ''; // Tyhjennä lista ennen uudelleen lataamista

            querySnapshot.forEach((doc) => {
                const li = document.createElement('li');
                li.textContent = `${doc.data().stuff}`;
                transactionsList.appendChild(li);
            });
        });
    }

    loadTransactions();
});


